import React, { useState, useContext, useEffect } from "react";
import { TodoContext } from "../provider/ToDoStore";

const Home = () => {
  const { todoData, error, loading, todoCnt, doneCnt } = useContext(
    TodoContext
  );

  const showStatus = () => {
    if (loading) return <h2>...</h2>;
    if (error) return <h2>  </h2>;

    return (
      <>
        <h2>    {todoCnt},</h2>
        <h2>  {doneCnt} .</h2>
      </>
    );
  };

  return (
    <>
      <h2>.</h2>
      <h2>    .</h2>
      {showStatus()}
    </>
  );
};

export default Home;
